import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import { Box, ButtonBase, IconButton, Typography } from "@material-ui/core";
import colors from "../../config/colors";
import { CloseRounded } from "@material-ui/icons";
import PropertyOwnerModalTextField from "./PropertyOwnerModalTextField";
function getModalStyle() {
  const top = 40;
  const left = 45;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 600,
    backgroundColor: theme.palette.background.paper,

    boxShadow: theme.shadows[5],
    padding: theme.spacing(4, 4, 4),
    outline: "none",
  },
  propertyTypesBtn: {
    width: "100%",
    height: 60,
    backgroundColor: colors.transCerise,
    borderRadius: 10,
    color: colors.white,
    fontSize: 20,
    [theme.breakpoints.down("xs")]: {
      fontSize: 12,
    },
  },
  modalTitle: {
    fontSize: 20,
    fontWeight: "bold",
    color: colors.primary,
  },
  modalDesc: {
    fontSize: 16,
    color: "#7C7C7C",
  },
  modal: {
    outline: "none",
  },
}));

export default function PropertyOwnerModal(props) {
  const classes = useStyles();
  const {
    // title,
    children,
  } = props;
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <Box display="flex" justifyContent="flex-end">
        <IconButton
          onClick={handleClose}
          style={{ transform: "translate(20px)" }}
        >
          <CloseRounded style={{ color: "#7C7C7C" }} />
        </IconButton>
      </Box>
      <Typography className={classes.modalTitle}>
        We will contact you as soon as possible
      </Typography>
      <Typography className={classes.modalDesc}>
        Please input your phone number
      </Typography>
      <div style={{ height: 20 }} />
      <PropertyOwnerModalTextField label="Phone Number 1" />
      <div style={{ height: 20 }} />
      <PropertyOwnerModalTextField label="Phone Number 2" />
      <div style={{ height: 20 }} />
      <Box display="flex" justifyContent="center" alignItems="center">
        <ButtonBase
          style={{
            backgroundColor: colors.cerise,
            color: colors.white,
            paddingLeft: 40,
            paddingRight: 40,
            paddingTop: 20,
            paddingBottom: 20,
            fontSize: 16,
            fontWeight: "bold",
            borderRadius: 10,
          }}
        >
          Submit
        </ButtonBase>
      </Box>
    </div>
  );

  return (
    <div>
      {/* <button type="button" onClick={handleOpen}>
        Open Modal
      </button> */}
      <ButtonBase onClick={handleOpen}>{children}</ButtonBase>
      <Modal
        className={classes.modal}
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}
