import React from "react";
import {
  Box,
  Container,
  Typography,
  // TextField,
  // Button,
  // Menu,
  // MenuItem,
  // InputAdornment,
  // InputBase,
  // IconButton,
  Grid,
  ButtonBase,
  Hidden,
} from "@material-ui/core";
import { useTranslation } from "react-i18next";
import Pagination from "@material-ui/lab/Pagination";
// import Slider from "infinite-react-carousel";
import colors from "../../config/colors";
import Search from "../../components/reusable/Search";
import DropDown from "../../components/reusable/DropDown";
import _ from "lodash";
import PropertiesCard from "./PropertiesCard";
import { makeStyles } from "@material-ui/core/styles";

import FilterModal from "./FilterModal";
const useStyles = makeStyles((theme) => ({
  titleBox: {
    height: 400,
    [theme.breakpoints.down("xs")]: { height: 300 },
    [theme.breakpoints.down("sm")]: { height: 300 },
  },
}));
const PropertiesUI = (props) => {
  const { t } = useTranslation();
  const classes = useStyles();
  const {
    propertyContext,
    subPropertyTypes,
    onPropertyClick,
    properties,
    locations,
    numberOfBathroom,
    handleBathroomChange,
    numberOfBedroom,
    dealTypes,
    sortByIndex,
    onSortByChange,
    maximumPrice,
    minimumPrice,
    handleLocationChange,
    handleSubPropertyTypeChange,
    handleBedroomChange,
    handleDealTypeChange,
    handleMinimumPriceChange,
    handleMaximumPriceChange,
    onPaginationChange,
    onSearchClick,
    pagination,
  } = props;

  return (
    <div>
      <Box
        className={classes.titleBox}
        style={{
          backgroundImage: 'url("/images/properties-wall.png")',
          backgroundPosition: "center",
          backgroundColor: "#8c8c8c",
          backgroundBlendMode: "multiply",
          backgroundSize: "cover",
        }}
        display="flex"
        alignItems="center"
        justifyContent="center"
        flexDirection="column"
      >
        <div style={{ height: 20 }} />
        <Typography
          variant="h3"
          style={{ fontWeight: "bold", color: colors.white }}
        >
          {t("Properties")}
        </Typography>
      </Box>

      <Container maxWidth="lg">
        <div style={{ height: 30 }} />
        <Hidden smDown lgUp>
          <Box display="flex" align="center" justifyContent="space-between">
            <DropDown
              label={t("Deal Type")}
              options={dealTypes}
              minWidth={80}
              handleChange={handleDealTypeChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Minimum Price")}
              minWidth={110}
              options={minimumPrice}
              handleChange={handleMinimumPriceChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Maximum Price")}
              minWidth={110}
              options={maximumPrice}
              handleChange={handleMaximumPriceChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Bedroom")}
              minWidth={80}
              options={numberOfBedroom}
              handleChange={handleBedroomChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Bathroom")}
              options={numberOfBathroom}
              minWidth={80}
              handleChange={handleBathroomChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Property Type")}
              minWidth={90}
              options={subPropertyTypes}
              selectedValue={propertyContext.selectedSubPropertyType}
              handleChange={handleSubPropertyTypeChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Location")}
              minWidth={140}
              options={locations}
              selectedValue={propertyContext.selectedLocation}
              handleChange={handleLocationChange}
            />
          </Box>
        </Hidden>
        <Hidden mdDown>
          <Box display="flex" align="center" justifyContent="space-between">
            <DropDown
              label={t("Deal Type")}
              options={dealTypes}
              minWidth={140}
              handleChange={handleDealTypeChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Minimum Price")}
              minWidth={170}
              options={minimumPrice}
              handleChange={handleMinimumPriceChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Maximum Price")}
              minWidth={170}
              options={maximumPrice}
              handleChange={handleMaximumPriceChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Bedroom")}
              options={numberOfBedroom}
              handleChange={handleBedroomChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Bathroom")}
              options={numberOfBathroom}
              handleChange={handleBathroomChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Property Type")}
              minWidth={150}
              options={subPropertyTypes}
              selectedValue={propertyContext.selectedSubPropertyType}
              handleChange={handleSubPropertyTypeChange}
            />
            <div style={{ width: 10 }} />
            <DropDown
              label={t("Location")}
              minWidth={200}
              options={locations}
              selectedValue={propertyContext.selectedLocation}
              handleChange={handleLocationChange}
            />
          </Box>
        </Hidden>
        <Hidden mdUp>
          <Grid container spacing={3}>
            <Grid item sm={3} xs={3}>
              <FilterModal
                minimumPrice={minimumPrice}
                maximumPrice={maximumPrice}
                numberOfBedroom={numberOfBedroom}
                numberOfBathroom={numberOfBathroom}
                subPropertyTypes={subPropertyTypes}
                locations={locations}
              />
            </Grid>
            <Grid item sm={9} xs={9}>
              <Search
                onClick={onSearchClick}
                placeholder={t("Search for listing using the unit name")}
              />
            </Grid>
          </Grid>
        </Hidden>
      </Container>
      <Hidden smDown>
        <div style={{ height: 30 }} />
        <Container maxWidth="lg">
          <Grid container>
            <Grid item xl={6} lg={6} md={6}>
              <Box display="flex" alignItems="center" textAlign="right">
                <Typography style={{ marginRight: 10, color: colors.primary }}>
                  {t("Sort by")}:
                </Typography>

                <ButtonBase
                  onClick={() => {
                    onSortByChange(0, "high_to_low_price");
                  }}
                >
                  <Box
                    borderRadius={5}
                    border={1}
                    borderColor={
                      sortByIndex === 0 ? colors.primary : colors.grey
                    }
                    style={{
                      backgroundColor:
                        sortByIndex === 0 ? colors.primary : colors.white,
                      padding: 10,
                      color: sortByIndex === 0 ? colors.white : colors.grey,

                      fontWeight: "bold",
                    }}
                  >
                    {t("Price (High-Low)")}
                  </Box>
                </ButtonBase>
                <div style={{ width: 10 }} />

                <ButtonBase
                  onClick={() => {
                    onSortByChange(1, "low_to_high_price");
                  }}
                >
                  <Box
                    borderRadius={5}
                    border={1}
                    borderColor={
                      sortByIndex === 1 ? colors.primary : colors.grey
                    }
                    style={{
                      backgroundColor:
                        sortByIndex === 1 ? colors.primary : colors.white,
                      padding: 10,
                      color: sortByIndex === 1 ? colors.white : colors.grey,

                      fontWeight: "bold",
                    }}
                  >
                    {t("Price (Low-High)")}
                  </Box>
                </ButtonBase>
                <div style={{ width: 10 }} />
                <ButtonBase
                  onClick={() => {
                    onSortByChange(2, "newest");
                  }}
                >
                  <Box
                    borderRadius={5}
                    border={1}
                    borderColor={
                      sortByIndex === 2 ? colors.primary : colors.grey
                    }
                    style={{
                      backgroundColor:
                        sortByIndex === 2 ? colors.primary : colors.white,
                      padding: 10,
                      color: sortByIndex === 2 ? colors.white : colors.grey,

                      fontWeight: "bold",
                    }}
                  >
                    {t("Newest")}
                  </Box>
                </ButtonBase>
              </Box>
            </Grid>
            <Grid item xl={6} lg={6} md={6}>
              <Search
                onClick={onSearchClick}
                placeholder={t("Search for listing using the unit name")}
              />
            </Grid>
          </Grid>
        </Container>
      </Hidden>
      <div style={{ height: 30 }} />
      <Container maxWidth="lg">
        <Grid container spacing={3}>
          {_.map(properties, (property) => {
            return (
              <Grid item xl={4} lg={4} md={4} sm={6} xs={6}>
                <PropertiesCard
                  onClick={() => {
                    onPropertyClick(
                      property.id,
                      property.propertyGroup.propertyType,
                      property.propertyGroup.zoneIdentifier
                    );
                  }}
                  spaceImage={property.propertyGroup.images[0].imageUrl}
                  dealType={property.dealType}
                  price={property.totalPrice}
                  area={property.area}
                  name={property.propertyGroup.projectName}
                  location={property.propertyGroup.zone}
                  numberOfBedrooms={property.numberOfBedrooms}
                  floor={property.floor}
                  orientation={property.orientation}
                />
              </Grid>
            );
          })}
        </Grid>
      </Container>
      <div style={{ height: 30 }} />
      <Container maxWidth="xl">
        <Box display="flex" justifyContent="center">
          <Pagination
            count={pagination.totalPage}
            variant="outlined"
            shape="rounded"
            siblingCount={3}
            boundaryCount={3}
            size="large"
            onChange={onPaginationChange}
          />
        </Box>
      </Container>
      <div style={{ height: 500 }} />
    </div>
  );
};
export default PropertiesUI;
